import { dbService } from "fbase";
import React, { useEffect, useRef, useState } from "react";
import CanvasDraw from "../../src/canvasDrawIndex";
import firebase from "firebase/compat/app";
import Hotkeys from "react-hot-keys";
import Videojs from "./video.js";
// import tagss from "./tags.json";
import testPost from "./testpost.json";
import "videojs-hotkeys";

// https://www.sakugabooru.com/tag.json?limit=0

const people = [
  { id: 14391, name: "ag", count: 0, type: 0, ambiguous: false },
  { id: 21666, name: "ab", count: 0, type: 0, ambiguous: false },
];

export default ({ userObj, isAndroid }) => {
  const [searchTerm, setSearchTerm] = useState("");
  const [videoUrl, setVideoUrl] = useState("");
  const [videoPage, setVideoPage] = useState(1);
  const [searchHeight, setSearchHeight] = useState(0);
  const [searchResults, setSearchResults] = useState([]);
  const [jsons, setJsons] = useState([]);
  const [booruResults, setBooruResults] = useState([]);
  const [booruTag, setBooruTag] = useState([]);
  const [booruUrl, setBooruUrl] = useState("");
  const [videoInfo, setVideoInfo] = useState([]);
  const [bookmarkLists, setBookmarkLists] = useState([]);
  const [videowidth, setVideowidth] = useState(450);
  const [bookmarkListRefresh, setBookmarkListRefresh] = useState(true);
  const [bookmarkListOn, setBookmarkListOn] = useState(false);
  const [herokuOn, setHerokuOn] = useState(true);
  const handleChange = (e) => {
    setSearchTerm(e.target.value);
    setSearchHeight(200);
  };
  useEffect(() => {
    const str = searchTerm.replace(" ", "_");
    
    
    const results = jsons.filter((tagg) =>
        tagg["name"].toLowerCase().includes(str)
     );

//     e.filter((tagg) =>
//     tagg["name"].toLowerCase().includes(str)
//  );
  //  });
    // const results = tagss.filter((tagg) =>
    //   tagg["name"].toLowerCase().includes(str)
    // );
    // console.log(typeof(searchTerm))
    // console.log(results)
    if (searchTerm != "") {
      setSearchResults(results);
    }
  }, [searchTerm]);

  const [Csubmit, setCsubmit] = useState("");
  const [Csubmits, setCsubmits] = useState([]);
  const [pinReload, setPinreload] = useState(0);
  const canvasRef = useRef();
  const [drawSetting, setDrawSetting] = useState({
    brushRadius: 0.8,
    lazyRadius: 0,
  });
  const [youtubeUrlIndex, setYoutubeUrlIndex] = useState(0);
  const [youtubeUrls, setYoutubeUrls] = useState([
    "PLtG4P3lq8RHGuMuprDarMz_Y9Fbw_d2ws",
    "PLTY4AtphBe8NS7WFIMIhHIBJVk3xQvi-q",
    "PLTY4AtphBe8PSqfXT4qfxwdbLgSK0XZ9v",
    "PLTY4AtphBe8P3zIOlyr7gEt176af-pp5R",
    "PLtG4P3lq8RHFBeVaruf2JjyQmZJH4__Zv",
    "PLHYRqaApAQGGCUm1mPME4HVOzay3OcTgH",
  ]);

  const [brushColor, setBrushColor] = useState("#444");
  const searchRef = useRef();

  useEffect(() => {
    import("./tags.json").then(math => {
      setJsons(math.default)
    });


    if (isAndroid) {
      setVideowidth(600);
    }
    dbService
      .doc("usersetting/" + userObj.uid)
      .get()
      .then((doc) => {
        if (doc.exists) {
          setDrawSetting({
            brushRadius: doc.data().brushRadius,
            lazyRadius: doc.data().lazyRadius,
          });

          //console.log(doc.data())
        } else {
          // doc.data() will be undefined in this case
          console.log("No such setting document!");
        }
      })
      .catch((error) => {
        console.log("Error getting document:", error);
      });

    setHerokuOn(JSON.parse(localStorage.getItem(1)));
  }, []);

  const refreshAndSubmit = () => {
    const data = canvasRef.current.getSaveData();
    const dataUrl = canvasRef.current.getDataURL();
    canvasRef.current.clear();

    dbService.collection("Csubmit").add({
      text: data,
      createdAt: Date.now(),
      creatorId: userObj.uid,
      drawingTime: 0,
      heart: 0,
      dataUrl: dataUrl,
      refPinUrl: "",
      //  refPhoto:{refPhoto},
      //pinNumber:randomPinNum,
    });

    setCsubmit("");

    //clearInterval(timeId.current);
  };

  const onSubmit = (event) => {
    event.preventDefault();

    refreshAndSubmit();
  };
  const onSubmitSearch = (event) => {
    event.preventDefault();
    setBookmarkListOn(false);
    refreshSearch();
  };

  const refreshSearch = () => {
    //   fetch(`https://croquistoolcors.herokuapp.com/https://www.sakugabooru.com/post.json?page=${videoPage}&tags=${searchTerm}`)
    //   .then((response)=>response.json())
    //  .then((data)=>{
    //    setBooruResults(data);

    //  })
    if (herokuOn) {
      fetch(
        `https://jolly-hall-d8e3.ddcmyk.workers.dev/?https://www.sakugabooru.com/post.json?page=${videoPage}&tags=${searchTerm}`
      )
        .then((response) => {
          if (response.ok) {
            return response.json();
            // setBooruResults(response.json());
          }
        })
        .then((data) => {
          console.log(":", data);

          console.log(typeof data);

          if (typeof data == "object") {
            setBooruResults(data);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    } else {
      fetch(
        `https://www.sakugabooru.com/post.json?page=${videoPage}&tags=${searchTerm}`
      )
        .then((response) => {
          if (response.ok) {
            return response.json();
            // setBooruResults(response.json());
          }
        })
        .then((data) => {
          console.log(":", data);

          console.log(typeof data);
          if (typeof data == "object") {
            setBooruResults(data);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    }

    // 
    // setBooruResults(testPost);
  };

  const onPrePageClick = (event) => {
    event.preventDefault();
    setVideoPage((pre) => pre - 1);
    refreshSearch();
  };
  const onForPageClick = (event) => {
    event.preventDefault();
    setVideoPage((pre) => pre + 1);
    refreshSearch();
  };
  const onDeleteClick = () => {
    const ok = window.confirm("wanna delete this?");
    if (ok) {
      dbService.doc(`sakugaBookmark/0`).delete();
    }
  };
  const onBookmarkListClick = (event) => {
    event.preventDefault();
    setBookmarkListOn(!bookmarkListOn);
    if (bookmarkListRefresh) {
      dbService
        .collection("sakugaBookmark")
        .where("creatorId", "==", userObj.uid)
        .orderBy("createdAt", "desc")
        // .endAt(Date.now()-listEnd)

        .limit(50)
        // .get((snapshot) =>{
        .onSnapshot((snapshot) => {
          const CsubmitArray = snapshot.docs.map((doc) => ({
            id: doc.id,
            ...doc.data(),
          }));

          setBookmarkLists(CsubmitArray);
          console.log(Csubmits);
          setBookmarkListRefresh(false);
        });
    }
  };
  const setBookMark = (event) => {
    event.preventDefault();
    const ok = window.confirm("wanna bookmark this?");
    if (ok) {
      dbService.collection("sakugaBookmark").add({
        DimgUrl: videoInfo[0],
        DvideoUrl: videoInfo[1],
        Dtag: videoInfo[2],
        creatorId: userObj.uid,
        createdAt: Date.now(),
        Did: videoInfo[3],
      });
    }
  };
  const onClearClick = () => {
    const ok = window.confirm("wanna clear this?");
    if (ok) {
      canvasRef.current.clear();
    }
  };
  const onUndoClick = () => {
    canvasRef.current.undo();
  };
  const onEraseClick = () => {
    setBrushColor("erase");
  };
  const onBrushClick = () => {
    setBrushColor("#444");
  };
  const onShadowClick = () => {
    setBrushColor("shadow");
  };

  const videoJsOptions = {
    autoplay: false,
    playbackRates: [0.2, 0.4, 0.6, 0.8, 1],
    width: videowidth,
    //height: 300,
    controls: true,
    controlBar: {
      pictureInPictureToggle: false,
      fullscreenToggle: false,
    },
    plugins: {
      framebyframe: {
        fps: 23.976024,
        steps: [
          { text: "< 1f", step: -1 },
          { text: "1f >", step: 1 },
        ],
      },
      hotkeys: {
        seekStep: 1 / 23.976024,
        enableModifiersForNumbers: false,
        customKeys: {
          DforwardKey: {
            key: function (event) {
              // Toggle something with CTRL + D Key
              return event.which === 68;
            },
            handler: function (player, options, event) {
              // Using mute as an example
              player.pause();
              player.currentTime(player.currentTime() + 1 / 23.976024);
            },
          },

          SrewindKey: {
            key: function (event) {
              // Toggle something with CTRL + D Key
              return event.which === 83;
            },
            handler: function (player, options, event) {
              // Using mute as an example
              player.pause();
              player.currentTime(player.currentTime() - 1 / 23.976024);
            },
          },
          E1forwardKey: {
            key: function (event) {
              // Toggle something with CTRL + D Key
              return event.which === 69;
            },
            handler: function (player, options, event) {
              // Using mute as an example
              player.pause();
              player.currentTime(player.currentTime() + 1);
            },
          },

          W1rewindKey: {
            key: function (event) {
              // Toggle something with CTRL + D Key
              return event.which === 87;
            },
            handler: function (player, options, event) {
              // Using mute as an example
              player.pause();
              player.currentTime(player.currentTime() - 1);
            },
          },
          R5forwardKey: {
            key: function (event) {
              // Toggle something with CTRL + D Key
              return event.which === 82;
            },
            handler: function (player, options, event) {
              // Using mute as an example
              player.pause();
              player.currentTime(player.currentTime() + 5);
            },
          },

          Q5rewindKey: {
            key: function (event) {
              // Toggle something with CTRL + D Key
              return event.which === 81;
            },
            handler: function (player, options, event) {
              // Using mute as an example
              player.pause();
              player.currentTime(player.currentTime() - 5);
            },
          },
        },
      },
    },

    sources: [
      {
        // src: "https://www.sakugabooru.com/data/ecf85b36125117bb806562628494f682.mp4",
        src: videoUrl,
        //type: 'video/mp4',
      },
    ],
  };

  return (
    <>
      <Hotkeys
        keyName="ctrl+z,c,x"
        onKeyDown={(keyName, e, handle) => {
          //console.log("test:onKeyDown", keyName);

          if (keyName == "ctrl+z") {
            onUndoClick();
          }
          if (keyName == "c") {
            if (brushColor != "erase") {
              onEraseClick();
            } else if (brushColor == "erase") {
              onBrushClick();
            }
          }
          if (keyName == "x") {
            if (brushColor != "shadow") {
              onShadowClick();
            } else if (brushColor == "shadow") {
              onBrushClick();
            }
          }
        }}
        // onKeyUp={(keyName, e, handle) => {
        //   console.log("test:onKeyUp", e, handle);
        //   setOutput(`onKeyUp ${keyName}`);
        // } }
      ></Hotkeys>

      <div>
        <div style={{ float: "right" }}></div>

        <div>
          <label>videos:</label>
          <input
            type="number"
            min="0"
            max={youtubeUrls.length - 1}
            style={{ width: "30px" }}
            value={youtubeUrlIndex}
            onChange={(e) => {
              setYoutubeUrlIndex(e.target.value);
              setPinreload(pinReload + 1);
            }}
          />
        </div>

        {/* <div>https://assets.pinterest.com/ext/embed.html?id={randomPinNum[pinID]}    {pinID}</div> */}
        {/*  */}
        {isAndroid ? (
          <>
            {/* <div style={{ float: 'left' }}>
        

        <form onSubmit={onSubmitSearch}>
        <input
        type="text"
        placeholder="Search"
        value={searchTerm}
        onChange={handleChange}
      />
       <input type={"submit"} value="search" />
       <button onClick={onPrePageClick}>{"<"}</button>
       {videoPage}
        <button onClick={onForPageClick}> {">"} </button>
        </form>
        
        
      
      
      <ul style={{width: '450px', height: searchHeight, overflow: 'auto'}}>
        {searchResults.map(item => (
        <li onClick={e=>{console.log(item['-name']); setSearchTerm(item['-name']);setSearchHeight(0); }}>{item['-name']}</li>
        ))}
      </ul>
      <ul style={{width: '450px', height:'400px', overflow: 'auto'}}>
      {booruResults.map(item => (
        <img src={item.preview_url} 
        style={{width: '215px'}} 
        onClick={e=>{setVideoUrl(item.file_url); console.log(item.file_url); setPinreload(pinReload + 1);}}></img>
        
        
        ))}
      </ul>
      
      <div key={pinReload}><Videojs  {...videoJsOptions} /></div>
        </div> */}

            <div style={{ float: "left" }}>
              <form onSubmit={onSubmitSearch}>
                <input
                  type="text"
                  placeholder="Search"
                  value={searchTerm}
                  onChange={handleChange}
                />
                <input type={"submit"} value="search" />
                <button onClick={onPrePageClick}>{"<"}</button>
                {videoPage}
                <button onClick={onForPageClick}> {">"} </button>
                <button onClick={onBookmarkListClick}> {"list"} </button>
                {bookmarkListOn ? (
                  <></>
                ) : (
                  <>
                    <button onClick={setBookMark}> {""} </button>
                  </>
                )}
              </form>

              {/* {searchTerm=null ?(<></>):(<></>)} */}

              <ul
                style={{
                  width: "450px",
                  height: searchHeight,
                  overflow: "auto",
                }}
              >
                {searchResults.map((item) => (
                  <li key={item.id}
                    onClick={(e) => {
                      console.log(item["name"]);
                      setSearchTerm(item["name"]);
                      setSearchHeight(0);
                    }}
                  >
                    {item["name"]}
                  </li>
                ))}
              </ul>
              <ul style={{ width: "450px", height: "400px", overflow: "auto" }}>
                {bookmarkListOn ? (
                  <>
                    {bookmarkLists.map((item) => (
                      <>
                        <div style={{ width: "215px", float: "left" }}>
                          <img
                            src={item.DimgUrl}
                            style={{ width: "215px" }}
                            onClick={(e) => {
                              setVideoUrl(item.DvideoUrl);
                              console.log(item.DvideoUrl);
                              setPinreload(pinReload + 1);
                            }}
                          ></img>

                          <div
                            style={{
                              width: "215px",
                              height: "0px",
                              transform: "translate( 0px, -25px )",
                            }}
                          >
                            <button
                              style={{
                                backgroundColor: "transparent",
                                border: "1px solid #3498db",
                                appearance: "none",
                                borderRadius: "0",
                              }}
                              onClick={(e) => {
                                setBooruTag(item.Dtag.split(/\s+/));
                                setBooruUrl(item.Did);
                              }}
                            >
                              {" "}
                              {"tag"}{" "}
                            </button>
                            <button
                              style={{
                                backgroundColor: "transparent",
                                border: "1px solid #3498db",
                                appearance: "none",
                                borderRadius: "0",
                              }}
                              onClick={(e) => {
                                const ok = window.confirm("wanna delete this?");
                                if (ok) {
                                  dbService
                                    .doc(`sakugaBookmark/${item.id}`)
                                    .delete();
                                }
                              }}
                            >
                              {" "}
                              {"del"}{" "}
                            </button>
                          </div>
                        </div>
                      </>
                    ))}
                  </>
                ) : (
                  <>
                    {booruResults.map((item) => (
                      <>
                        <div style={{ width: "215px", float: "left" }}>
                          <img
                            src={item.preview_url}
                            style={{ width: "215px" }}
                            onClick={(e) => {
                              setVideoUrl(item.file_url);
                              console.log(item.file_url);
                              setPinreload(pinReload + 1);
                              setVideoInfo([
                                item.preview_url,
                                item.file_url,
                                item.tags,
                                item.id,
                              ]);
                            }}
                          ></img>

                          <div
                            style={{
                              width: "215px",
                              height: "0px",
                              transform: "translate( 0px, -25px )",
                            }}
                          >
                            <button
                              style={{
                                backgroundColor: "transparent",
                                border: "1px solid #3498db",
                                appearance: "none",
                                borderRadius: "0",
                              }}
                              onClick={(e) => {
                                setBooruTag(item.tags.split(/\s+/));
                                setBooruUrl(item.id);
                              }}
                            >
                              {" "}
                              {"tag"}{" "}
                            </button>
                          </div>
                        </div>
                      </>
                    ))}
                  </>
                )}
              </ul>
              <div style={{ width: "450px" }}>
                {booruTag.map((tagItem) => (
                  <>
                    <button
                      onClick={(e) => {
                        setSearchTerm(tagItem);
                      }}
                    >
                      {" "}
                      {tagItem}{" "}
                    </button>
                  </>
                ))}
              </div>
              <div key={pinReload}>
                <Videojs {...videoJsOptions} />
              </div>
            </div>
          </>
        ) : (
          <>
            {/*  */}
            <div style={{ float: "left" }}>
              <div key={pinReload}>
                <Videojs {...videoJsOptions} />
              </div>

              <form onSubmit={onSubmitSearch}>
                <input
                  type="text"
                  placeholder="Search"
                  value={searchTerm}
                  onChange={handleChange}
                />
                <input type={"submit"} value="search" />
                <button onClick={onPrePageClick}>{"<"}</button>
                {videoPage}
                <button onClick={onForPageClick}> {">"} </button>
                <button onClick={onBookmarkListClick}> {"list"} </button>
                {bookmarkListOn ? (
                  <></>
                ) : (
                  <>
                    <button onClick={setBookMark}> {""} </button>
                  </>
                )}
              </form>

              {/* {searchTerm=null ?(<></>):(<></>)} */}

              <ul
                style={{
                  width: "450px",
                  height: searchHeight,
                  overflow: "auto",
                }}
              >
                {searchResults.map((item) => (
                  <li key={item.id}
                    onClick={(e) => {
                      console.log(item["name"]);
                      setSearchTerm(item["name"]);
                      setSearchHeight(0);
                    }}
                  >
                    {item["name"]}
                  </li>
                ))}
              </ul>
              <ul style={{ width: "450px", height: "400px", overflow: "auto" }}>
                {bookmarkListOn ? (
                  <>
                    {bookmarkLists.map((item) => (
                      <>
                        <div style={{ width: "215px", float: "left" }}>
                          <img
                            src={item.DimgUrl}
                            style={{ width: "215px" }}
                            onClick={(e) => {
                              setVideoUrl(item.DvideoUrl);
                              console.log(item.DvideoUrl);
                              setPinreload(pinReload + 1);
                            }}
                          ></img>

                          <div
                            style={{
                              width: "215px",
                              height: "0px",
                              transform: "translate( 0px, -25px )",
                            }}
                          >
                            <button
                              style={{
                                backgroundColor: "transparent",
                                border: "1px solid #3498db",
                                appearance: "none",
                                borderRadius: "0",
                              }}
                              onClick={(e) => {
                                setBooruTag(item.Dtag.split(/\s+/));
                                setBooruUrl(item.Did);
                              }}
                            >
                              {" "}
                              {"tag"}{" "}
                            </button>
                            <button
                              style={{
                                backgroundColor: "transparent",
                                border: "1px solid #3498db",
                                appearance: "none",
                                borderRadius: "0",
                              }}
                              onClick={(e) => {
                                const ok = window.confirm("wanna delete this?");
                                if (ok) {
                                  dbService
                                    .doc(`sakugaBookmark/${item.id}`)
                                    .delete();
                                }
                              }}
                            >
                              {" "}
                              {"del"}{" "}
                            </button>
                          </div>
                        </div>
                      </>
                    ))}
                  </>
                ) : (
                  <>
                    {booruResults.map((item) => (
                      <>
                        <div style={{ width: "215px", float: "left" }}>
                          <img
                            src={item.preview_url}
                            style={{ width: "215px" }}
                            onClick={(e) => {
                              setVideoUrl(item.file_url);
                              console.log(item.file_url);
                              setPinreload(pinReload + 1);
                              setVideoInfo([
                                item.preview_url,
                                item.file_url,
                                item.tags,
                                item.id,
                              ]);
                            }}
                          ></img>

                          <div
                            style={{
                              width: "215px",
                              height: "0px",
                              transform: "translate( 0px, -25px )",
                            }}
                          >
                            <button
                              style={{
                                backgroundColor: "transparent",
                                border: "1px solid #3498db",
                                appearance: "none",
                                borderRadius: "0",
                              }}
                              onClick={(e) => {
                                setBooruTag(item.tags.split(/\s+/));
                                setBooruUrl(item.id);
                              }}
                            >
                              {" "}
                              {"tag"}{" "}
                            </button>
                          </div>
                        </div>
                      </>
                    ))}
                  </>
                )}
              </ul>
              <div style={{ width: "450px" }}>
                {booruTag.map((tagItem) => (
                  <>
                    <button
                      onClick={(e) => {
                        setSearchTerm(tagItem);
                      }}
                    >
                      {" "}
                      {tagItem}{" "}
                    </button>
                  </>
                ))}
                <a
                  target={"_blank"}
                  href={"https://www.sakugabooru.com/post/show/" + booruUrl}
                >
                  link
                </a>
                <button
                  onClick={(e) => {
                    localStorage.setItem(1, !herokuOn);
                    setHerokuOn((pre) => !pre);
                  }}
                >
                  {" "}
                  {herokuOn ? <>on</> : <>off</>}
                </button>
                s,d: qwer:
              </div>
            </div>
          </>
        )}
        {/*  */}

        <div style={{ float: "left" }}>
          <div>
            <div style={{ float: "right" }}>
              <label>Lazy:</label>
              <input
                type="number"
                min="0"
                max="100"
                style={{ width: "30px" }}
                value={drawSetting.lazyRadius}
                onChange={(e) =>
                  setDrawSetting((prevState) => ({
                    ...prevState,
                    lazyRadius: e.target.value,
                  }))
                }
              />
            </div>
            <div style={{ float: "right" }}>
              {brushColor == "erase" ? (
                <>
                  <label>Erase Size:</label>
                  <input
                    type="number"
                    min="1"
                    max="100"
                    style={{ width: "30px" }}
                    value={drawSetting.eraseRadius}
                    onChange={(e) =>
                      setDrawSetting((prevState) => ({
                        ...prevState,
                        eraseRadius: e.target.value,
                      }))
                    }
                  />
                </>
              ) : (
                <>
                  <label>Brush Size:</label>
                  <input
                    type="number"
                    min="1"
                    max="100"
                    style={{ width: "30px" }}
                    value={drawSetting.brushRadius}
                    onChange={(e) =>
                      setDrawSetting((prevState) => ({
                        ...prevState,
                        brushRadius: e.target.value,
                      }))
                    }
                  />
                </>
              )}
            </div>
            <div style={{ float: "right" }}>
              <button onClick={onClearClick}>Clear Canvas</button>
            </div>
            <div style={{ float: "right" }}>
              {isAndroid ? (
                <>
                  <button style={{ padding: "15px" }} onClick={onUndoClick}>
                    
                  </button>
                </>
              ) : (
                <>
                  <button onClick={onUndoClick}></button>
                </>
              )}
            </div>

            <div style={{ float: "right" }}>
              {brushColor == "erase" ? (
                <>
                  <button onClick={onBrushClick}>brush</button>
                </>
              ) : (
                <>
                  <button onClick={onEraseClick}>erase</button>
                </>
              )}
              <div style={{ float: "right" }}>
                {brushColor == "shadow" ? (
                  <>
                    <button onClick={onBrushClick}>brush</button>
                  </>
                ) : (
                  <>
                    <button onClick={onShadowClick}>shadow</button>
                  </>
                )}

                {brushColor}
              </div>
            </div>
            <div style={{ float: "right" }}>
              <button onClick={onSubmit}>Upload Drawing</button>
            </div>
          </div>
          <div style={{ float: "right" }}>
            <CanvasDraw
              canvasWidth={600}
              canvasHeight={600}
              brushRadius={
                brushColor == "erase"
                  ? drawSetting.eraseRadius
                  : drawSetting.brushRadius
              }
              lazyRadius={drawSetting.lazyRadius}
              ref={canvasRef}
              brushColor={brushColor}
            />
          </div>
        </div>
      </div>
    </>
  );
};
