import { dbService } from "fbase";
import React, { useEffect, useRef,useState } from "react";
import CanvasDraw from "../canvasDrawIndex";
import { LazyLoadImage } from 'react-lazy-load-image-component';
import   firebase from "firebase/compat/app";

const CCsubmitbook =({CsubmitbookObj, isOwner,viewDeleyToggle})=>{
    const [editing,setEditing]= useState(false);
    const [newCsubmitbook,setnewCsubmitbook]=useState(CsubmitbookObj.text);
    // const [givedHeart,setGivedHeart]=useState(false)
    // const [yourheart,setYourHeart]=useState(CsubmitbookObj.heart);
    const canvasRef2 = useRef();
    const [onOff,setOnOff]=useState(true);
    // const [onReview,setOnReview]= useState(false);
    // const [offReview,setoffReview]= useState(false);
    // // canvasRef2.current.loadSaveData(CsubmitbookObj.text);
    let uploadDay= new Date(CsubmitbookObj.createdAt);
    const [viewRefPin,setViewRefpin]=useState(false);
    const [refUrl,setRefUrl]=useState("");
    // useEffect(() => {
    //    
    //     //  if(viewsToggle && onOff){
    //     //     canvasRef2.current.loadSaveData(CsubmitbookObj.text,true);
    //     //     setOnOff(false)
    //     //  }
        
    // },[editing]);
    const onDeleteClick= () => {
        const ok = window.confirm("wanna delete this?")
        if(ok){
            dbService.doc(`Csubmitbook/${CsubmitbookObj.id}`).delete();
            
        }
    };
    const toggleReview=()=> {
        canvasRef2.current.clear();
        // canvasRef2.current.loadSaveData(CsubmitbookObj.text);
        canvasRef2.current.loadSaveData(CsubmitbookObj.text);
        
    };
    const offReview=()=>{
        setEditing(false);
            //  
            // console.log('none')
            // const dataUrl=canvasRef2.current.getDataURL();
        
    
        
            // dbService.doc(`Csubmitbook/${CsubmitbookObj.id}`).update({
            
            // dataUrl:dataUrl,
           //  refPhoto:{refPhoto},
            //pinNumber:randomPinNum,
        // });
        
    }
    const onReview=()=> {setEditing(true);
        setTimeout(function() {
            canvasRef2.current.loadSaveData(CsubmitbookObj.text);
            
            
          }, 5);
        }
    const onSubmit = (event)=> {
        event.preventDefault();
        const data = canvasRef2.current.getSaveData();
        const dataUrl=canvasRef2.current.getDataURL();
        dbService.doc(`Csubmitbook/${CsubmitbookObj.id}`).update({
            text:data,
            dataUrl:dataUrl
        });
        setEditing(false);

    }
    const onChange = (event)=> {
        const {target:{value},
        }=event;
        setnewCsubmitbook(value);
    }
    //const toggleheart=()=> setYourHeart(prev => !prev);
    const onHeartClick= async() =>{
        setOnOff(prev => !prev);
        await dbService.doc(`Csubmitbook/${CsubmitbookObj.id}`).update({
           heart:(1+(CsubmitbookObj.heart)),
        })
        console.log(CsubmitbookObj.heart)
        const pinDocRef= dbService.doc(`RefDB/refStrings`);
        pinDocRef.update({
            heart:firebase.firestore.FieldValue.arrayUnion(CsubmitbookObj.refPinUrl)
        });
    }


    
     
    return(
        <div   style={{float:'left', border: '10px solid rgb(0 97 255 / 7%)',width:"420px"
        }}>
            
        {
            editing ? (
            <>
            <button onClick={onSubmit}>Edit</button>
            {/* <form onSubmit={onSubmit}><input value={newCsubmitbook} required onChange={onChange}/>
            <input type={"submit"} value="edit"/>
            </form> */}
            
            <div><CanvasDraw 
            // style={{ touchAction: 'disabled'  ? 'auto' : 'none' }}
            ref={canvasRef2}
            //  disabled
            // hideInterface
            brushRadius={0.6}
              lazyRadius={0.5}
              
              brushColor={"#df6161"}
              allowOnlyPointerType={"pen"}
             loadTimeOffset={5}
             /></div>
             
             <button onClick={toggleReview}>Rewind</button>
             <button onClick={offReview}>Cancel</button>
             
            </>
            ):(
            <>
            
            <div style={{margin: '0 auto'}}>
            <LazyLoadImage
            
      
      width={400}
      src={CsubmitbookObj.dataUrl} // use normal <img> attributes as props
       />
      {/* <img src={CsubmitbookObj.dataUrl} height="400px" ></img> */}
      </div>
            
            
        {isOwner ? (
            <>

            {/* <button disabled> {CsubmitbookObj.heart}</button> */}
            {/* <button onClick={onHeartClick} style={{padding: "0px 0px"}}>{CsubmitbookObj.heart}</button> */}
            <button onClick={onHeartClick} style={{padding: "0px 0px"}}>{CsubmitbookObj.heart==0 ? (<></>):(<>+{CsubmitbookObj.heart}</>)}</button>
            <button onClick={onDeleteClick}>Delete</button>
            <button onClick={onReview}>Review</button>
            
            
            </>
        ):(
            <>
            <button onClick={onHeartClick}> {CsubmitbookObj.heart}</button>
            
            </>
        )}

            
            
                
            
            
            
            
        </>
            
            
            
            

            )}
            <button onClick={()=>{setViewRefpin(pre=>!pre);setRefUrl(CsubmitbookObj.refPinUrl)}}>Ref Pin</button>
            {"   "}  {uploadDay.toLocaleString(navigator.language || navigator.userLanguage)}
            {viewRefPin ? (<>
                <div>
                    <div><button onClick={e=>{
                        const ok = window.confirm("wanna redraw this?")
                        if(ok){
                            if((typeof(CsubmitbookObj.refPinUrl)=="string")&&(CsubmitbookObj.refPinUrl.includes("firebasestorage.googleapis.com"))){
                        localStorage.setItem("redraw", CsubmitbookObj.refPinUrl)

                    }else{
                        localStorage.setItem("redraw", "https://assets.pinterest.com/ext/embed.html?id="+CsubmitbookObj.refPinUrl)
                    }
                        }}}>redraw</button></div>

                    {(CsubmitbookObj.refPinUrl.startsWith("pot.com/o/"))?(<>
                        <img  src={CsubmitbookObj.refPinUrl.replace("pot.com/o/", "https://firebasestorage.googleapis.com/v0/b/croquistool.appspot.com/o/")}  width={415} frameBorder={0} scrolling="no"></img>
                    </>):(<>
                    
                     
                    {(typeof(CsubmitbookObj.refPinUrl)=="string")&&(CsubmitbookObj.refPinUrl.includes("firebasestorage.googleapis.com"))?(  
                    // (console.log(typeof(CsubmitbookObj.refPinUrl))
                    <>
                    
                        <img  src={CsubmitbookObj.refPinUrl}  width={415} frameBorder={0} scrolling="no"></img>
                    </>
                    ):(<>
                        <iframe  src={"https://assets.pinterest.com/ext/embed.html?id="+CsubmitbookObj.refPinUrl} height="600" width="300" frameBorder={0} scrolling="no" ></iframe>
                    </>)}
                    </>)}  
                </div></>):(<></>)}
    </div>
)
};
    

export default CCsubmitbook;