import { dbService, storageService } from "fbase";
import React, { useEffect, useRef, useState } from "react";
import CanvasDraw from "../../src/canvasDrawIndex";
import   firebase from "firebase/compat/app";
import PinchZoomPan from "PinchZoomPan";
import Hotkeys from "react-hot-keys";
import { set } from "lodash";


export default ({userObj,isAndroid}) => {
    const [Csubmit,setCsubmit]= useState("");
    // const [Csubmits,setCsubmits]= useState([]);
    const [pinReload,setPinreload]=useState(0);
    const canvasRef3 = useRef();
    const [drawSetting,setDrawSetting]=useState({
       
        brushRadius:1,
        lazyRadius:0,
       
        


    })
    const [listst,setListst]= useState([])
    const [listst2,setListst2]= useState([])

    const [sotyIndex,setSotyIndex]=useState(0);
    const [sotyUrlarr,setSotyUrlarr]=useState([]);

    const [brushColor,setBrushColor]=useState("#444")
    const [randomOn,setRandomOn]=useState(false)
    const [extendOn,setExtendOn]=useState(false)
    const [drawingOn,setDrawingOn]=useState(false)
    const [min,setMin]=useState(0);
    const [sec,setSec]=useState(0);
    const [croquisTime,setCroquisTime]=useState(300);
    const time=useRef(croquisTime);
    const timeId=useRef(null);

    function sexagesimal(i) {
      if(i <= 9){
          return "0"+i;
      }else{
          return i;
      }
  }
    useEffect(()=> {
      if(localStorage.getItem(2) != null){
        setCroquisTime(JSON.parse(localStorage.getItem(2)))
      }
      
     
        timeId.current= setInterval(()=>{
          
            setMin(parseInt(time.current/60));
            setSec(time.current % 60);
            time.current -= 1;
        },1000);
        return()=> clearInterval(timeId.current);
    },[]);

    useEffect(()=> {
        if(time.current<=-1){
          if(randomOn){
            refreshAndSubmit();
            randomIndex()
          }

            
            
        }
    },[sec]);

    const randomIndex= () => {
      setSotyIndex(Math.floor(Math.random()*690) + 1)
  };

    useEffect(()=> {
        dbService.doc("usersetting/"+userObj.uid).get().then((doc) => {
            if (doc.exists) {
                setDrawSetting({
                    brushRadius:doc.data().brushRadius,
                    lazyRadius:doc.data().lazyRadius,
                    sotyIndex:doc.data().sotyIndex,})
                    setSotyIndex(doc.data().sotyIndex)
                console.log(doc.data())
                
                
               
                
                
            } else {
                // doc.data() will be undefined in this case
                console.log("No such setting document!");
            }
        }).catch((error) => {
            console.log("Error getting document:", error);
             
        });



        dbService.doc(`RefDB/soty`).get().then((doc) => {
       if (doc.exists) {
           //console.log("Document data:", doc.data().refUrl);
           //setRandomPinNum(doc.data().refUrl);
           //console.log("doc",doc.data().refUrl)
           const urlarr=doc.data().refUrl;
           
           
           setSotyUrlarr(urlarr);
          
           
           
       } else {
           // doc.data() will be undefined in this case
           console.log("No such document!");
       }
   }).catch((error) => {
       console.log("Error getting document:", error);
        
   });
    },[]);
            
    
 const ongetUrl= async(event) => {
        
        
        // const listRef = ref(storageService, "soty/");

        // Find all the prefixes and items.
        storageService.ref().child("soty/").listAll()
        .then((res) => {
            res.prefixes.forEach((folderRef) => {
            // All the prefixes under listRef.
            // You may call listAll() recursively on them.
            });
            res.items.forEach(async(itemRef) => {
            // All the items under listRef.
            let urls= await itemRef.getDownloadURL().then(function(url) {
                return url;
              }).catch(function(error) {
                console.log(error)
                // Handle any errors here
              });
              
              setListst(prevState=>[...prevState,urls])
              
            
            
            });
        }).catch((error) => {
            console.log(error)
            // Uh-oh, an error occurred!
        });


        // const res = await storageService.ref().child("soty/").listAll()
        
        
        //        res.items.forEach(async(itemref)=>{
        //         list.push(await itemref.getDownloadURL())
                
        //     })
        var set = new Set(listst);
         console.log(set)
         const arr = [...set]
         console.log(arr) 
         setListst2(arr);
         

    }
    const submitsoty =()=>{ 
        
            console.log(listst2)
            const pinDocRef= dbService.doc(`RefDB/soty`);
            pinDocRef.update({
            
                refUrl:listst2})
         
    }
    
    const refreshAndSubmit =()=>{
      if(randomOn){
        time.current=croquisTime;}
            
        dbService.collection("usersetting").doc(userObj.uid).update(
            {
            "sotyIndex":sotyIndex,
            }
        );
            
            const data = canvasRef3.current.getSaveData();
            const dataUrl=canvasRef3.current.getDataURL();
            canvasRef3.current.clear();
        
            
            dbService.collection("Csubmit").add({
                text:data,
                createdAt: Date.now(),
                creatorId: userObj.uid,
                drawingTime:0,
                heart:(0),
                dataUrl:dataUrl,
                refPinUrl:sotyUrlarr[sotyIndex],
                sotyIndex:sotyIndex,
               //  refPhoto:{refPhoto},
                //pinNumber:randomPinNum,
            });
            
            setCsubmit(""); 
            if(randomOn){
              
              randomIndex()
            }
            //clearInterval(timeId.current);
    }
   

   


     const onSubmit= (event) => {
         event.preventDefault();
     
        refreshAndSubmit();
     };
     
     


     const onClearClick= () => {
      const ok = window.confirm("wanna clear this?")
      if(ok){
        canvasRef3.current.clear();
          
      }
    };
    const onUndoClick= () => {
      canvasRef3.current.undo();
  };
   const onEraseClick= () => {
      setBrushColor("erase")
      
  };
   const onBrushClick= () => {
      setBrushColor("#444")
      
  };
  const onShadowClick= () => {
    setBrushColor("shadow")
    
};
  const onPrePageClick= (event) => {
    event.preventDefault();
      
      setSotyIndex(pre=>pre-1);
    
  };
   const onForPageClick= (event) => {
    event.preventDefault();
    setSotyIndex(pre=>pre+1);
    
  };
  const onChangeTime = (event)=> {
    const {
        target:{value},
    }=event;
    setCroquisTime(value);
    localStorage.setItem(2, value)
  }

  return(
    <><Hotkeys
      keyName="ctrl+z,c,x"
      onKeyDown={(keyName, e, handle) => {
        //console.log("test:onKeyDown", keyName);
        if (keyName == "ctrl+z") { onUndoClick(); }
        if (keyName == "c") {
          if (brushColor != "erase") { onEraseClick(); }
          else if (brushColor == "erase") { onBrushClick(); }
        }
        if (keyName == "x") {
          if (brushColor != "shadow") { onShadowClick(); }
          else if (brushColor == "shadow") { onBrushClick(); }
        }
      } }
    ></Hotkeys>
    <div>
      {randomOn ?(<><div style={{
          position: 'absolute',
          left: '50%',
          transform: 'translateX(-50%)',
          width: '70px',
          textAlign: 'center',

          background: 'linear-gradient(to left,#FFC365 ' + (min * 60 + sec) * 100 / croquisTime + '%,#E8E8E8 ' + (min * 60 + sec) * 100 / croquisTime + '%)'
        }}>
          {min}: {sexagesimal(sec)}
        </div></>):(<></>)}
      

        <div style={{ float: 'right' }}>

          {/* <button onClick={ongetUrl}>geturls</button>
    <button onClick={submitsoty}>submitsoty</button>
     */}
        </div>






        <div>


          <label>pages:</label>
          <input
            type="number"
            min="0"
            max={sotyUrlarr.length - 1}
            style={{ width: "40px" }}
            value={sotyIndex}
            onChange={e => {
              setSotyIndex(e.target.value);
              setPinreload(pinReload + 1);
            } } />
            <button onClick={onPrePageClick}>{"<"}</button>
            <button onClick={onForPageClick}>{">"}</button>
            <button onClick={e=>{setRandomOn(pre=>!pre);time.current=croquisTime}}>random {randomOn?(<>on</>):(<>off</>)}</button>
            <button onClick={e=>{setExtendOn(pre=>!pre)}}>Extend</button>
            <button onClick={e=>{setDrawingOn(pre=>!pre)}}>Drawing Only</button>
            <div style={{ float: 'right' }}>
            {randomOn?(<>
              
              <input value={croquisTime} onChange={onChangeTime} type={"number"} placeholder="Time" />
              Set Time

            </>):(<></>)}</div>
       

        </div>
        

        {/* <div>https://assets.pinterest.com/ext/embed.html?id={randomPinNum[pinID]}    {pinID}</div> */}
        <div style={{ float: 'left' }}>
          {/* <iframe key={pinReload} src={"https://www.youtube.com/embed/videoseries?list="+youtubeUrls[youtubeUrlIndex]+"&cc_load_policy=1&cc_lang_pref=ko"} height={450} width={450} frameBorder={0} scrolling="no" ></iframe> */}
            {isAndroid?(<>
              <div style={{ width: '600px', height: '450px' }}>
            <PinchZoomPan>
              <img alt='Test Image' src={sotyUrlarr[sotyIndex]} />
            </PinchZoomPan>
          </div>
            </>):(<>
              {drawingOn?(<><div style={{ width: '1px', height: '770px' }}><PinchZoomPan>
              <img alt='Test Image' src={sotyUrlarr[sotyIndex]} />
            </PinchZoomPan></div></>):(<>
            {extendOn?(<>
            <div style={{ width: '850px', height: '770px' }}><PinchZoomPan>
              <img alt='Test Image' src={sotyUrlarr[sotyIndex]} />
            </PinchZoomPan></div>
              </>):(<>
              <div style={{ width: '450px', height: '770px' }}><PinchZoomPan>
              <img alt='Test Image' src={sotyUrlarr[sotyIndex]} />
            </PinchZoomPan></div>
                </>)}
                </>)}
            
          
          </>)}

        </div>

        <div style={{ float: 'left' }}>
          <div>



            <div style={{ float: 'right' }}>
              <label>Lazy:</label>
              <input
                type="number"
                min="0"
                max="100"
                style={{ width: "30px" }}
                value={drawSetting.lazyRadius}
                onChange={e => setDrawSetting(prevState => ({ ...prevState, lazyRadius: e.target.value }))} />
            </div>
            <div style={{ float: 'right' }}>
            {brushColor=="erase" ? (<>
                <label>Erase Size:</label>
              <input
                type="number"
                min="1"
                max="100"
                style={{ width: "30px" }}
                value={drawSetting.eraseRadius}
                onChange={e => setDrawSetting(prevState => ({ ...prevState, eraseRadius: e.target.value }))} />

              </>):(<>
                <label>Brush Size:</label>
              <input
                type="number"
                min="1"
                max="100"
                style={{ width: "30px" }}
                value={drawSetting.brushRadius}
                onChange={e => setDrawSetting(prevState => ({ ...prevState, brushRadius: e.target.value }))} />
              
              </>)}
            </div>
            <div style={{ float: 'right' }}>
              <button onClick={onClearClick}>Clear Canvas</button>
            </div>
            <div style={{ float: 'right' }}>
              {isAndroid?(<><button style={{padding:"15px"}} onClick={onUndoClick}></button></>):(<><button onClick={onUndoClick}></button></>)}
            </div>

            <div style={{ float: 'right' }}>
              {(brushColor == "erase") ? (
                <><button onClick={onBrushClick}>brush</button></>
              ) : (<><button onClick={onEraseClick}>erase</button></>)}

            <div style={{ float: 'right' }}>
              {(brushColor == "shadow") ? (
                <><button onClick={onBrushClick}>brush</button></>
              ) : (<><button onClick={onShadowClick}>shadow</button></>)}

            {brushColor}
            </div>  
            </div>
            <div style={{ float: 'right' }}>

              <button onClick={onSubmit}>Upload Drawing</button>
            </div>


          </div>
          <div style={{ float: 'right' }}>
            <CanvasDraw
              
              enablePanAndZoom= {true}
              canvasWidth={(drawingOn?(1000):(600))}
              canvasHeight={600}
              brushRadius={(brushColor=="erase") ? drawSetting.eraseRadius : drawSetting.brushRadius}
              lazyRadius={drawSetting.lazyRadius}
              ref={canvasRef3}
              brushColor={brushColor} />
          </div>
        </div>




      </div></>
  )
 };