import React, { useRef, useState, useEffect } from 'react';

const DragSprite = ({imagesrc,  imageWidth}) => {
    
  
    const spriteImage = imagesrc; // URL of the sprite image
 
    const [spriteRows, setSpriteRows] = useState(1);
    const [spriteCols, setSpriteCols] = useState(1);
    const [spriteNum, setSpriteNum] = useState(1);
    const [currenIndex, setCurrenIndex] = useState(0);
 
    const spriteRow = 0; // Initial x-coordinate of the sprite in the sprite image
    const spriteCol = 0; // Initial y-coordinate of the sprite in the sprite image
   
    const [spriteWidth, setSpriteWidth] = useState(100);
    const [spriteHeight, setSpriteHeight] = useState(100);
    const [calScale, setCalScale] = useState(0.5);
    const [modelName,setModelName]= useState("none")
    useEffect(() => {
        
        
        const img = new Image();
        img. src = "https://firebasestorage.googleapis.com/v0/b/croquistool.appspot.com/o/"+spriteImage+"?alt=media";
        const str = spriteImage;
        console.log(typeof(str))
        let col =1;
        let row =1;
        
        let num =1;
        if(str != undefined){
          //d360d%2Fcesar047_3_8_24_-crouching.jpg
          const words = str.split(/[_]/);
        console.log(words)
        //https://firebasestorage.googleapis.com/v0/b/croquistool.appspot.com/o/d360d%2Fcesar047_3_8_24_-crouching.jpg?alt=media&token=4b6a8c5b-3f08-4cb6-94a4-d63c4f7a5cd4
        const inde= words[0].indexOf("%2F");
        const modelname =words[0].substring(inde + 3)
            setModelName(modelname)
         col =Number(words[1]);
         row =Number(words[2]);
        
         num =Number(words[3])
        }
        
        // setColNum([col,num])
        setSpriteCols(col)
        setSpriteRows(row);
        setSpriteNum(num)
        img.onload = () => {
            
            console.log("",imageWidth,"",img.width,"",)
            
            
          setSpriteWidth(img.width / col);
          setSpriteHeight(img. height / row);
          console.log(col,num,spriteImage)
          setCalScale(imageWidth/(img.width / col))
        };
      
      }, [spriteImage]);
    const spriteStyle = {
        // width: spriteWidth,
        // height: spriteHeight,
       
        width: spriteWidth ,   // reduce the width of the div element
            height: spriteHeight , // reduce the height of the div element
            //  backgroundSize: '400px',
             
        backgroundImage: `url(${"https://firebasestorage.googleapis.com/v0/b/croquistool.appspot.com/o/"+spriteImage+"?alt=media"})`,
        backgroundPosition: `-${spriteRow * spriteWidth}px -${spriteCol * spriteHeight}px`,
      };
    
      const spriteRef = useRef(null);
    
      const [position, setPosition] = useState({ Row: spriteRow, Col: spriteCol });
     
     
      // const handleMouseDown = (e) => {
      //   const initialX = e.clientX;
        
      //   const inix = position.Row;
      //   const iniy = position.Col;
      //   const iniindex= 4*iniy+inix
      //   console.log("",iniindex,spriteNum)
      //   const onMouseMove = (e) => {
      //     const deltaX = e.clientX - initialX;
      //   //   currentX = e.clientX;
      //       let indexx=parseInt(deltaX / 20)
      //       let currentindex=iniindex+indexx
      //       // console.log(currentindex)
            
      //       if(currentindex<0){
      //           currentindex=spriteNum+(currentindex % (spriteCols*spriteRows))
      //       }
      //       if(currentindex>spriteNum-1){
      //           currentindex=currentindex-spriteNum
      //       }
      //       // console.log(currentindex)
      //       setCurrenIndex(currentindex)
      //       let Row= (currentindex)%spriteCols
      //       let Col= parseInt(currentindex/spriteCols)
            
      //       if(Row<0){Row=0}
      //       else if(Row>spriteCols){Row=0}
      //       if(Col<0){Col=0}
      //       else if(Col>spriteRows){Col=0}
      //     setPosition({Row,Col});
          
      //   };
      //   window.addEventListener('mousemove', onMouseMove);
      //   window.addEventListener('mouseup', () => {
      //     window.removeEventListener('mousemove', onMouseMove);
      //   });
      // };
      const handlePointerDown = (e) => {
        const initialX = e.clientX;
        
        const inix = position.Row;
        const iniy = position.Col;
        const iniindex= 4*iniy+inix
        console.log("",iniindex,spriteNum)
        let inindex = currenIndex;
        const onPointerMove = (e) => {
          const deltaX = e.clientX - initialX;
        //   currentX = e.clientX;
            let indexx=parseInt(deltaX / 20)
            let currentindex=inindex+indexx
            // console.log(currentindex)
            
            if(currentindex<0){
                currentindex=spriteNum+(currentindex % (spriteCols*spriteRows))
            }
            if(currentindex>spriteNum-1){
                currentindex=currentindex-spriteNum
            }
            // console.log(currentindex)
            setCurrenIndex(currentindex)
            let Row= (currentindex)%spriteCols
            let Col= parseInt(currentindex/spriteCols)
            
            if(Row<0){Row=0}
            else if(Row>spriteCols){Row=0}
            if(Col<0){Col=0}
            else if(Col>spriteRows){Col=0}
          setPosition({Row,Col});
          
        };
        window.addEventListener('pointermove', onPointerMove);
        window.addEventListener('pointerup', () => {

          window.removeEventListener('pointermove', onPointerMove);
        });
      };
    
      
    
      return (<>
       {currenIndex+1}/{spriteNum}   {modelName} <a href={"https://firebasestorage.googleapis.com/v0/b/croquistool.appspot.com/o/"+spriteImage+"?alt=media"} target="_blank">link</a>
        <div 
        style={{ transform: 'scale('+calScale+')',
        transformOrigin: "top left",
        width: imageWidth+"px",
        }}
        
        >

        
        <div
          ref={spriteRef}
          style={{
            ...spriteStyle,
            touchAction : "none",
            backgroundPosition: `-${position.Row * spriteWidth}px -${position.Col * spriteHeight}px`,
          }}
          // onMouseDown={handleMouseDown}
          onPointerDown={handlePointerDown}
          
        />
        </div>
        </> 
      );
    };
    
    export default DragSprite;